const fill = (input) => {
    const addZero = `0${input}`;
    return addZero.substr(addZero.length - 2, 2);
};
export const formatTime = (sec) => {
    if (sec < 0) {
        return '00:00';
    }
    const minute = Math.floor(sec / 60);
    const second = sec % 60;
    return `${fill(minute)}:${fill(second)}`;
};
