export const convertToReadOnlyOptions = (options, userAnswer, correctAnswer) => {
    return options.map((option) => ({
        value: option.id,
        displayText: option.content,
        clz: (correctAnswer === option.id)
            ? 'correct'
            : (userAnswer === option.id)
                ? 'wrong'
                : 'not-set',
    }));
};
